<template>
    <div class="kechenglist">
        <div v-for="item in kechenglist" :key="item.name">
            <div class="wenzhang" @click="goTohtml(item)">
                <div class="kuai">
                    <div class="textkuang">
                        <div class="title">{{item.title}}</div>
                        <div class="minlan">
                            <div class="laiyuan">{{item.count}}次学习</div>
                            <div class="yigoumai">¥{{item.jiage}}</div>
                            <div class="yigoumai" v-if="item.yigoumai">去学习</div>
                            <div class="weigoumai" v-if="item.yigoumai==0">去试读</div>
                        </div>
                    </div>
                    <img class="image" :src="item.image" mode="scaleToFill" lazy-load="false">
                </div>
            </div>
        </div>
        <div class="textend">恋爱脱单话术</div>
    </div>
</template>

<script>
    import api from '@/api/index';
    import wx from 'weixin-js-sdk';
    export default {
        async activated() {
            if (!this.$route.query.code) {
                var href =
                    'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx2cc1bc5a412d44d2&redirect_uri=http%3A//www.lianaizhuli.com/kechenglist&response_type=code&scope=snsapi_userinfo&state=123#wechat_redirect';
                window.location.href = href;
            } else {
                if (this.unionid == '') {
                    const json = await api.getFwhnionid({
                        code: this.$route.query.code
                    });
                    this.$route.query.code = false;
                    this.unionid = json.data.unionid;
                    localStorage.setItem('unionid', this.unionid);
                }
                this.getKechengList();
            }
        },
        data() {
            return {
                unionid: '',
                kechenglist: [],
                scroll: ''
            };
        },
        methods: {
            async getKechengList() {
                let json = await api.getKechengList({
                    unionid: this.unionid,
                    scroll: this.scroll
                });
                this.kechenglist = json.data;
            },
            goTohtml(item) {
                this.$router.push({
                    path: './kecheng',
                    query: {
                        json: item
                    }
                });
            }
        }
    };
</script>

<style lang="less">
    .kechenglist {
        background: #ffffff;
        .wenzhang {
            background: #ffffff;
            width: 92%;
            margin-left: 4%;
            padding-top: 10px;
            padding-bottom: 10px;
            border-bottom: 1px solid #efefef;
            .kuai {
                display: flex;
                justify-content: space-between;
                .textkuang {
                    width: 80%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    .title {
                        color: #333;
                        text-align: left;
                        font-size: 15px;
                    }
                    .minlan {
                        width: 100%;
                        display: flex;
                        justify-content: space-between;
                        text-align: center;
                        height: 10px;
                        line-height: 10px;
                        .laiyuan {
                            font-size: 10px;
                        }
                        .yigoumai {
                            width: 40px;
                            color: #fff;
                            background: #ff7e00;
                            border-radius: 5px;
                            font-size: 10px;
                        }
                        .weigoumai {
                            width: 40px;
                            color: #fff;
                            background: #09bb07;
                            border-radius: 5px;
                            font-size: 10px;
                        }
                    }
                }
                .image {
                    margin-left: 16px;
                    height: 70px;
                    width: 124px;
                }
            }
        }
    }
    .textend {
        padding-top: 30px;
        padding-bottom: 30px;
        text-align: center;
    }
</style>



